@media (max-width: 800px){
    .burger-btn {
        display: block;
        text-align: center;
        width: 25px;
        margin: 0 auto;
    }
    .sidebar li{
        display: none;
    }
    .sidebar .listAddBtn{
        display: none;
    }
    .sidebar {
        transition: .3s;
        padding-top: 18px;
        max-width: 50px;
        min-width: 50px;
        padding-left: 5px;
        padding-right: 5px;
        margin: 0 auto;
    }
    .sidebar.active{
        min-width: 250px;
        position: absolute;
    }
    .sidebar.active .burger-btn{
        left: 100px;
        margin-bottom: 30px;
    }
    .sidebar.active li{
        display: flex;
    }
    .sidebar.active .listAddBtn{
        display: flex;
    }
    .content-item__title-wrapper i {
        display: block;
    }
    
    .taskList__item .taskList__item-iconDelete {
        visibility: visible;
        opacity: .3;
        transition: .3s;
    }
    .content-item__task > svg {
        opacity: 1;
    }
    .content-item__task .content-item__checkbox label i {
        display: block;
    }
}

@media (max-width: 400px){
    .tasksAbsentTitle {
        font-size: 15px;
    }
    .sidebar {
        max-width: 35px;
        min-width: 35px;
    }
    .burger-btn span {
        width: 20px;
    }
    .burger-btn {
        width: 20px;
    }
    .content-item__title {
        font-size: 21px;
    }
    .content-item__taskAddButton span:nth-child(2) {
        font-size: 12px;
    }
    .content-item__taskAddButton span:nth-child(1) {
        height: 22px;
    }
    .content-item__taskAddButton > span svg {
        width: 12px;
        height: 12px;
    }
    .content-item__tasks {
        margin-top: 18px;
    }
    .content-item__task {
        column-gap: 8px;
    }
    .content-item__task p {
        font-size: 11px;
    }
    .content {
        padding: 16px;
    }
    .content-item__form input {
        height: 31px;
        padding: 7px 9px;
        font-size: 10px;
    }
    .content-item__form-buttons button {
        padding: 10px 11px;
        font-size: 10px;
    }
    .content-item__form {
        display: flex;
        flex-direction: column;
        row-gap: 10px;
    }
    .sidebar.active {
        min-width: 230px;
    }
    .sidebar.active .burger-btn {
        left: 93px;
    }
    .taskList__item-text {
        font-size: 14px;
        line-height: 16px;
    }
    .content-item__title-wrapper > i > svg{
        width: 12px;
    }
}