
.addButtonPopUp {
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    position: fixed;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 235px;
    height: 148px;
    border-radius: 10px;
    background: #FFF;
    box-shadow: 0px 6px 10px 0px rgba(0, 0, 0, 0.05);
    box-sizing: border-box;
    padding: 18px;
}

.addButtonPopUp input{
    width: 100%;
    height: 32px;
    box-sizing: border-box;
    padding: 8px 11px;
    outline: none;
    border-radius: 4px;
    border: 1px solid #EFEFEF;
    background: #FFF;
}
.addButtonPopUp div:first-child{
    position: absolute;
    top: 0;
    left: 100%;
    transform: translate(-50%, -50%);
}
.addButtonPopUp div:hover{
    cursor: pointer;
}
.addButtonPopUp__cancel path{
    fill: rgb(139, 139, 139);
    transition: .3s;
}
.addButtonPopUp div:hover .addButtonPopUp__cancel path{
    fill: rgb(63, 63, 63);
    transition: .3s;
}
.addButtonPopUp__addBtn{
    width: 100%;
    border-radius: 4px;
    background: #4DD599;  
    outline: none; 
    border: none; 
    height: 37px;
    transition: .3s;
}
.addButtonPopUp__addBtn:hover{
    background-color: #55e7a7;
    transition: .3s;
    cursor: pointer;
}
.addButtonPopUp__colors{
    display: flex;
    justify-content: space-between;
}
.circle{
    display: block;
    flex-shrink: 0;
    width: 16px;
    height: 16px;
    border-radius: 50%;
    border: 2px solid transparent;
}

.circle.active{
    border: 2px solid rgb(27, 226, 203);
}