span{
    height: 18px;
    line-height: 1;
}
.sidebar > .taskList__item{
    margin-bottom: 30px;
}
.taskList__items{
    list-style-type: none;
    padding: 0;
    width: 100%;
}

.taskList__item{
    display: flex;
    align-items: center;
    column-gap: 10px;
    padding: 10px 10px;
    margin-bottom: 5px;
    border-radius: 4px;
}
.taskList__item:hover{
    cursor: pointer;
    background: #FFF;
    border-radius: 3px;
    transition: .3s;
}
.taskList__item-iconDelete svg{
    opacity: 0.3;
    transition: .3s;
}
.taskList__item-iconDelete svg:hover{
    opacity: 1;
    transition: .3s;
}
.taskList__item-iconDelete{
    visibility: hidden;
    opacity: 0;
    transition: .3s;
}
.taskList__item:hover .taskList__item-iconDelete{
    visibility: visible;
    opacity: 1;
    transition: .3s;
}
.taskList__item span:first-child{
    flex-shrink: 0;
}

.taskList__item.active{
    background: #FFF;
    box-shadow: 0px 2px 10px 0px rgba(0, 0, 0, 0.05);
}
.taskList__item-icon{
    width: 13px;
    height: 13px;
    background-color: #27b417;
    border-radius: 50%;
}
.taskList__item-icon svg{
    display: none;
}
.taskList__item-text{
    width: 100%;
    text-align: left;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
    font-weight: 500;
}
